<template>
    <div class="login">
        <form
            action="#"
            id="login-form"
            class="d-flex justify-content-center align-items-center flex-wrap"
        >
            <div class="username" data-field-container="username">
                <label for=""></label>
                <input
                    type="text"
                    name="username"
                    autocomplete="on"
                    data-field
                    value=""
                />
                <p data-field-error></p>
            </div>
            <div class="pwd" data-field-container="pwd">
                <label for=""> </label>
                <input type="password" name="pwd" data-field value="" />
                <p data-field-error></p>
            </div>
            <div class="other">
                <button @click="onLogin"></button>
                <div class="d-flex justify-content-end">
                    <span class="flex-grow-1">xxx</span>
                    <router-link
                        custom
                        to="/account/regist"
                        v-slot="{ navigate }"
                        ><span role="link" @click="navigate">regist</span>
                    </router-link>
                    <router-link custom to="/home/mood" v-slot="{ navigate }"
                        ><span role="link" @click="navigate">home</span>
                    </router-link>
                </div>
            </div>
        </form>
    </div>
</template>

<script>
import { ref } from 'vue'
import router from '@/routes/index.js'
import useLoginValidatorInstance from '@/compositions/account/useLoginValidatorInstance.js'
import $http from '@/axios/http.js'
import { ElMessage } from 'element-plus'

export default {
    setup() {
        const validatorInstanceRef = ref(null) //
        useLoginValidatorInstance(validatorInstanceRef) // onMounted,instancevalidator...
        // button-
        const onLogin = function(e) {
            e.preventDefault()
            const validator = validatorInstanceRef.value
            const formData = validator.getFormData()
            const isPass = validator.doValidatorForm()
            const data = {}
            for (const key in formData) {
                data[key] = formData[key][0]
            }

            if (isPass.formPass) {
                $http
                    .post('/admin/account/login', {
                        data: data
                    })
                    .then(res => {
                        sessionStorage.token = res.token
                        ElMessage({
                            type: 'success',
                            message: '!'
                        })
                        router.push('/home/mood')
                    })
            } else {
                validator.setStatus()
                ElMessage({
                    type: 'danger',
                    message: '!'
                })
            }
        }
        return {
            onLogin
        }
    }
}
</script>

<style lang="scss">
@import '../../assets/scss/account/login.scss';
</style>
